import {gql} from "@apollo/client"
export const GET_ALL_QUOTES = gql`
  query getAllQuery {
    quotes {
      name
      by {
        _id
        firstName
      }
    }
  }
`;

export const GET_PROFILE = gql`
  query getProfile{
    user:myProfile {
      firstName
      lastName
      email
      quotes {
        name
      }
    }
  }
`;

export const GET_USER_BY_ID = gql`
  query getUserById($userid: ID!) {
    user(_id: $userid) {
      _id
      firstName
      email
      quotes {
        name
      }
    }
  }
`;